body {
  margin: 0;
  padding: 0;
  background-color: aquamarine;
}

.App {
  text-align: center;
  margin: 0;
  padding: 0;
  background-color: #ffffff;
  display: flex;
  flex-direction: column;
  overflow-x: hidden;
  /* check this as it may effect printing in a4 */
}

.p1 {
  width: 100vw;
  height: calc(100vw * (21 / 29.7));
  background: linear-gradient(to right, #ff6c6c, #ffa4a4);
}

.p1 h1 {
  margin: 0;
}

.p1-left {
  width: 50%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.p1-text {
  /* background-color: antiquewhite; */
  padding-left: 1vw;
  padding-right: 1vw;
  text-align: left;
  border-left: 0.3vw solid #ffffff;
  color: #ffffff;
  font-size: 2vw;
}

.p2 {
  width: 100vw;
  height: calc(100vw * (21 / 29.7));
  /* background-color: rgb(255, 168, 128); */
  font-size: 1.5vw;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.p2 h1,
.p2 h2,
.p2 h3 {
  color: #ff6c6c;
}

.p2-header {
  width: 100%;
  height: 10%;
  /* background-color: #fce5d8; */
  display: flex;
  align-items: center;
  justify-content: center;
}

/* .p2-header h1, */
.p2-header h1 {
  margin: 0;
  padding: 0;
  /* background-color: aqua; */
}

.p2-header-text {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.p2-body {
  height: 82%;
  width: 90%;
  display: flex;
  /* background-color: #fdcbae; */
}

.p2-body-left {
  width: calc(100% / 3);
  height: 100%;
  /* background-color: #724c36; */
}
.p2-body-centre {
  width: calc(100% / 3);
  height: 100%;
  /* background-color: #c29379; */
}

.p2-body-right {
  width: calc(100% / 3);
  height: 100%;
  /* background-color: #967c6e; */
}

.p2-footer {
  width: 100%;
  height: 8%;
  /* background-color: antiquewhite; */
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: baseline;
}

.p2-footer h3 {
  margin: 0;
  padding-left: 2vw;
}
